$days-size: 100px;
$gap: 1px;
$padding: 10px;
$transition: all 0.5s ease;
$hundred-percent: 100%;
$flex: flex;
$center: center;
$thirty-pixels: 30px;
$none: none;

$start-day:
  mon 0,
  tue 1,
  wed 2,
  thu 3,
  fri 4,
  sat 5,
  sun 6;

%number-properties {
  height: $hundred-percent;
  display: $flex;
  justify-content: $center;
  align-items: $center;
  font-size: $thirty-pixels;
}
